<template>
    <div>
        <div class="row">
            <div class="col-sm-8 mx-auto">
                <h2>Login</h2>
            </div>
        </div>
        <div class="row">
            <div class="col-sm-8 mx-auto">
                <form @submit.prevent="handleSubmit">
                    <div class="row">
                        <div class="form-group col-sm-5">
                            <label for="username">Email</label>
                            <input type="text" v-model="username" id="username" name="username" class="form-control" :class="{ 'is-invalid': submitted && !username }" placeholder="Enter email" />
                            <div v-show="submitted && !username" class="invalid-feedback">Username is required</div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="form-group col-sm-5">
                            <div class="form-group">
                                <label for="password">Password</label>
                                <input type="password" v-model="password" id="password" name="password" class="form-control" :class="{ 'is-invalid': submitted && !password }" placeholder="Password" />
                                <div v-show="submitted && !password" class="invalid-feedback">Password is required</div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="form-group col-sm-5">
                            <div class="form-group">
                                <button class="btn btn-primary" :disabled="loggingIn">Login</button>
                                <img v-show="loggingIn" src="data:image/gif;base64," />
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                username: '',
                password: '',
                submitted: false
            }
        },
        computed: {
            loggingIn() {
                return this.$store.state.authentication.status.loggingIn;
            }
        },
        created() {
            // reset login status
            this.$store.dispatch('authentication/logout');
        },
        methods: {
            handleSubmit(e) {
                this.submitted = true;
                const { username, password } = this;
                const { dispatch } = this.$store;
                if (username && password) {
                    dispatch('authentication/login', { username, password });
                }
            }
        }
    };
</script>